*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
@media screen and (prefers-reduced-motion: reduce) {
  *,
*::before,
*::after {
    animation-duration: 0.001ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.001ms !important;
  }
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

img,
picture,
svg {
  max-width: 100%;
  display: block;
}

button {
  font: inherit;
}

ul[role=list],
ol[role=list] {
  list-style: none;
}

.container {
  width: min(68.5rem, 100% - 2rem);
  margin-inline: auto;
  display: flex;
}

.container--wide {
  width: min(82.5rem, 100% - 2rem);
  margin-inline: auto;
  display: flex;
}

a {
  text-decoration: none;
  color: #fff;
}

.active {
  user-select: none;
}

html {
  font-family: "Roboto", sans-serif;
}

h1,
h2,
h3 {
  font-weight: 700;
}

.header {
  display: flex;
  flex-direction: column;
  background-color: #b50014;
}
.header .header__nav {
  width: 100%;
  max-height: 6rem;
  display: flex;
  justify-content: space-between;
  font-weight: 700;
  position: relative;
}
.header .header__nav .logo {
  max-width: 11rem;
  z-index: 999;
  position: absolute;
}
@media only screen and (max-width: 40em) {
  .header .header__nav .logo {
    max-width: 6rem;
    left: -1rem;
  }
}
.header .header__nav .navigation {
  display: flex;
  justify-content: flex-end;
  flex: 1;
  align-items: center;
  gap: 2.5rem;
  padding-block: 2rem;
}
.header .header__nav .navigation .nav-link {
  user-select: none;
}
.header .header__nav .navigation a {
  transition: 0.4s;
}
.header .header__nav .navigation a:hover {
  color: #252525;
}
.header .header__nav .navigation a:hover path {
  fill: #252525;
}
.header .header__nav .navigation path {
  transition: 0.4s;
}
.header .header__nav .navigation .download {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  user-select: none;
}
@media only screen and (max-width: 40em) {
  .header .header__nav .navigation .download {
    font-size: 0.875rem;
  }
}
.header .header__nav .navigation .hamburger {
  display: none;
  user-select: none;
  appearance: none;
  border: none;
  background: none;
  cursor: pointer;
}
.header .header__nav .navigation .hamburger:hover span {
  background-color: #252525;
}
.header .header__nav .navigation .hamburger:hover span:nth-child(2) {
  transform: translateX(0.25rem);
}
.header .header__nav .navigation .hamburger.active span:nth-child(1) {
  transform: translate(0.1rem, 0.125rem) rotate(45deg);
}
.header .header__nav .navigation .hamburger.active span:nth-child(2) {
  opacity: 0;
}
.header .header__nav .navigation .hamburger.active span:nth-child(3) {
  transform: translateY(-0.1rem) rotate(-45deg);
}
.header .header__nav .navigation .hamburger.active:hover {
  background-color: #252525;
}
.header .header__nav .navigation .hamburger span {
  display: block;
  width: 1.75rem;
  height: 0.2rem;
  background-color: #fff;
  border: 0;
  outline: 0;
  border-radius: 0.5rem;
  transform-origin: 0 0;
  transition: 0.4s;
}
@media only screen and (max-width: 40em) {
  .header .header__nav .navigation {
    gap: 1rem;
    padding-block: 0.7rem;
  }
  .header .header__nav .navigation .nav-link {
    display: none;
    font-size: 0.875rem;
  }
  .header .header__nav .navigation .lnr {
    line-height: 1.5;
    font-size: 2rem;
    font-weight: 600;
    padding-inline: 0.25rem;
  }
  .header .header__nav .navigation .hamburger {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 0.5rem;
  }
}
.header .working-hours {
  background-color: #252525;
  color: #fff;
  display: flex;
}
.header .working-hours .hours {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 0.875rem;
  padding-block: 0.4rem;
  gap: 0.4rem;
}
@media only screen and (max-width: 40em) {
  .header .working-hours .hours {
    font-size: 0.625rem;
    text-align: right;
    padding-block: 0.2rem;
  }
  .header .working-hours .hours img {
    display: none;
  }
  .header .working-hours .hours strong::after {
    content: "\a";
    white-space: pre;
  }
}

